body{
    display:flex;
    flex-direction: column;
    align-items: center;
    font-family:Arial, Helvetica, sans-serif;
}
.App{
    background-color:white;
    padding:10px 30px 0 30px;
    border-radius:4px;
}
h4, h1, p{
    margin:0;
}
.VideoList{
    width:420px;
}
.VideoList img{
    height:150px;
    margin:0 5px 0 0;
}
.VideoDescription{
    width:560px;
    margin:0 15px 0 50px;
}
.header{
    text-align:center;
    margin:0 0 20px 0;
}
.header input{
    width:400px;
    margin:10px;
    height:20px;
}
.mainbody{
    display:flex;
}
.VideoSide{
    display:flex;
    margin:0 10px 10px 0;
}
.VideoSide h4{
    font-size:14px;
}

@media only screen and (max-width: 1000px) {
    .App{
        background-color:white;
        padding:10px 30px 0 30px;
        border-radius:4px;
    }
    .box{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .mainbody{
        display:flex;
        flex-direction: column;
        align-items: center;
    }
    h4, h1, p{
        margin:0;
    }
    .VideoList{
        width:420px;
    }
    .VideoList img{
        height:250px;
        margin:0 5px 0 0;
    }
    .VideoDescription{
        width:80%;
        margin:0;
    }
    .the_video_iframe{
        width:100%;
    }
    .header{
        text-align:center;
        margin:0 0 20px 0;
    }
    .header input{
        width:400px;
        margin:10px;
        height:20px;
    }
    .VideoSide{
        display:flex;
        flex-direction: column;
        margin:10px 10px 10px 0;
    }
    .VideoSide h4{
        font-size:14px;
    }
    
  }